import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 1, 
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			orderByValue: 1,//排序值 0-综合,1-销量,2-价格低到高,3-价格高到低
			categoryId: '',
			productList: [],
			totalCount: 0,
			pageNo: 0,
		};
	},
	
	onLoad(options){



		this.categoryId = options.categoryId;
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			this.pageNo ++;
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			if(type === 'refresh'){
				this.pageNo = 1;
				this.productList = [];
			}
			
			uni.request({
				url: this.baseUrl + '/api/category/product/list',
				data: {
					categoryId: this.categoryId,
					orderByValue: this.orderByValue,
					pageNo: this.pageNo,
					pageSize: 10,
				},
				header: {},
				success: (res) => {
					if (res.data.success) {
						this.productList = this.productList.concat(res.data.result.records);
						this.totalCount = res.data.result.total
						this.loadingType  = this.productList.length + 1 > this.totalCount ? 'nomore' : 'more';
					}
				},
				complete: (res) => {
					if(type === 'refresh'){
						if(loading == 1){
							uni.hideLoading()
						}else{
							uni.stopPullDownRefresh();
						}
					}
				},
			});
			
			
			
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
				this.orderByValue = this.priceOrder === 1 ? 2: 3;
			}else{
				this.priceOrder = 0;
				this.orderByValue = index;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let productId = item.id;
			uni.navigateTo({
				url: `/pages/product/product?productId=${productId}`
			})
		},
	},
}
